<template>
  <div class="center studentLearningStatus" v-loading.body="loading">
    <div class="center_breadcrumb">
      <Crumb/>
    </div>
    <div class="center_content">
      <!-- 导出 -->
      <!-- <div class="center_content_title">
        <div class="derive-btn">
          <el-button type="success" icon="el-icon-tickets" round size="mini" @click.native.prevent="exportList">导出名单</el-button>
        </div>
      </div> -->
      <div class="center_content_search" style="margin-bottom: 0; border-radius: 10px 10px 0 0">
        <div style="margin-bottom: 60px; font-size:24px;font-family:PingFang SC;font-weight:bold;color:rgba(255,255,255,1);text-align:center; position: relative">请先按条件查询您所要查询的学年、课程及所带班级
          <div style="width:120px;height:28px;border:1px solid rgba(176,249,224,1);border-radius:14px;font-size:14px;line-height:28px;float: right;cursor: pointer; position: absolute; top: 0; right: 0" @click="exportList">
            <SvgIcon icon-class="icondaochu-excel"></SvgIcon>  导出Excel</div>
        </div>
        <!-- 搜索 -->
        <el-form ref="listQuery" :model="listQuery" label-width="100px">
          <el-row>
            <el-col :span="6">
              <el-form-item label="所属学年:">
                <el-select v-model="listQuery.grade" placeholder="请选择所属学年" @keyup.enter.native="search" @change="gradeChange">
                  <el-option
                    v-for="item in yearOptions"
                    :key="item.grade"
                    :label="`${item.grade} - ${item.grade + 1}学年`"
                    :value="item.grade"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="课程名称:">
                <el-select v-model="listQuery.courseId" placeholder="请先选择课程" @keyup.enter.native="search" @change="courseChange">
                  <el-option
                    v-for="item in curriculumOptions"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="带课班级:">
                <el-select v-model="listQuery.classId" placeholder="请先选择所属学年和课程名称" @keyup.enter.native="search">
                  <el-option
                    v-for="item in classOptions"
                    :key="item.id"
                    :label="item.name"
                    :value="item.id"
                  ></el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item label="搜索:">
                <el-input
                  class="search"
                  @keyup.enter.native="search"
                  v-model="listQuery.search"
                  placeholder="学号 姓名"
                  clearable>
                </el-input>
              </el-form-item>
            </el-col>
            
            
          </el-row>
          <el-row style="margin-top: 20px">
            <el-col :span="24">
              <div style="width: 270px;height: 50px;background: #fff;border-radius: 25px;text-align: center; color: #03B187; line-height: 50px;margin: 0 auto;font-weight: bold;font-size: 18px; cursor: pointer" @click="search">查  询</div>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <div class="table-container" style="border-radius: 0 0 10px 10px">
        <!-- 表格 -->
        <div v-if="flag" style="text-align: center; padding: 50px 0 150px;">
          <img src="@/assets/img/img-xuexizhuangtaichaxun.png" alt="" style="width: 500px">
          <p style="font-size:20px;font-family:PingFang SC;font-weight:bold;color:rgba(3,177,135,1);margin-top: 49px;">选择学年、课程、班级查看学生的学习状态</p>
        </div>
        <el-table v-else :data="tableData" style="width: 100%" @selection-change="handleSelectionChange" stripe>
          <el-table-column prop="code" label="学号"></el-table-column>
          <el-table-column prop="name"  label="姓名"></el-table-column>
          <el-table-column prop="className"  label="所属班级"></el-table-column>
          <el-table-column v-for="(info, index) in tableInfo" :key="index"  :label="info.grade">
            <template slot-scope="scope">
              <!-- <div>
                {{scope.row['val' + (index + 1)] >= scope.row['targetVal' + (index + 1)] && scope.row['val' + (index + 1)] != 0 ? '达到预期值' : scope.row['val' + (index + 1)] == scope.row['targetVal' + (index + 1)] && scope.row['val' + (index + 1)] == 0 ? '未达到预期值' : '未达到预期值'}}
              </div> -->
              <div>
                {{scope.row['targetVal' + (index + 1)] == 0 ? '未开始此能力的学习' : scope.row['val' + (index + 1)] >= scope.row['targetVal' + (index + 1)] ? '达到预期值' : '未达到预期值'}}
              </div>
            </template>
          </el-table-column>
          <el-table-column  label="标记备注">
            <template slot-scope="scope" v-if="scope.row.comment">
              <div>
                <SvgIcon icon-class="iconbiaoji"></SvgIcon>  {{scope.row.comment}}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="210" >
            <template slot-scope="scope">
              <div class="operate-button distance">
                <!-- <i class="el-icon-search"></i> -->
                <el-button
                 type="text"
                 @click.native.prevent="viewDetail(scope.row)"
                 size="small">
                 <SvgIcon icon-class="iconxuexizhuangtaixiangqing"></SvgIcon>&nbsp;&nbsp;查看详情
                </el-button>
              </div>
              <div v-if="scope.row.mark==0" class="operate-button distance">
                <el-button
                 @click.native.prevent="sign(scope.row)"
                 type="text"
                 size="small"
                 style="width: 68.55px;">
                 <SvgIcon icon-class="iconbiaoji-kong"></SvgIcon>
                </el-button>
              </div>
              <div v-else-if="scope.row.mark==1" class="operate-button distance">
                <el-button
                 @click.native.prevent="cancelSign(scope.row)"
                 type="text"
                 size="small"
                 style="width: 68.55px;">
                 <SvgIcon icon-class="iconbiaoji"></SvgIcon>
                </el-button>
              </div>
              
            </template>
          </el-table-column>
        </el-table>
        <!-- 分页 -->
        <div class="center_pagination" v-show="!flag">
          <el-pagination
           @size-change="handleSizeChange"
           @current-change="handleCurrentChange"
           background
           :current-page="listQuery.pageNum"
           :page-sizes="[15, 30, 40, 50]"
           :page-size="listQuery.pageSize"
           layout="prev, pager, next, jumper, sizes, total"
           :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
    <!-- 弹框 -->
    <el-dialog
      title="学习状态分析详情"
      :visible.sync="detailDialogVisible"
      :close-on-click-modal="false"
      width="40%">
      <div style="font-size:16px; margin-bottom: 20px"><span style="font-weight: 700">学生姓名： </span>{{name}}</div>
      <div style="font-size:16px">
        <div style="font-weight: 700; margin-bottom: 10px">学习状态： </div>
        <div class="detailBox" style="line-height: 30px; font-size: 14px;text-indent: 2em;" v-html="html"></div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="detailDialogVisible = false" size="small">取 消</el-button>
        <el-button type="primary" @click="detailDialogVisible = false" size="small">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
     title="添加标记备注"
     :visible.sync="signDialogVisible"
     :close-on-click-modal="false"
     width="40%">
      <div style="margin-bottom:10px;"><span style="color: red">*</span>添加备注：</div>
      <el-input
       type="textarea"
       :rows="10"
       resize='none'
       placeholder="备注信息不超过150字"
       v-model="content"
       maxlength="150">
      </el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="signDialogVisible = false" size="small">取 消</el-button>
        <el-button type="primary" @click="handleSubmuit" size="small">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "StudentLearningStatus",
  components: {},
  data() {
    return {
      name: '',
      html: '',
      flag: true,
      total: 0,
      detailDialogVisible: false,
      signDialogVisible: false,
      content: '',
      loading: false,
      tableInfo: [],
      listQuery: {
        grade: '',
        courseId: '',
        classId: '',
        search: '',
        pageNum: 1,
        pageSize: 15
      },
      pageTitle: "学生学习状态查询",
      yearOptions: [],
      curriculumOptions: [],
      classOptions: [],
      tableData: [],
      ids: '',
      studentId: '',
      signDetail: {}
    }
  },
  mounted() {
    this.loading = true
    const versionId= localStorage.getItem('versionId')
    this.$post(`/teacher/learningState/gradeList/${versionId}`).then(res => {
      if(res.status===1) {
        this.yearOptions = res.data
      } else {
        this.$notify.error({
          title: '提示',
          message: '获取所属学年失败！请刷新重试',
          duration: 2000
        })
      }
      this.loading = false
    }).catch(err => {
      this.$notify.error({
        title: '提示',
        message: '获取所属学年失败！请刷新重试',
        duration: 2000
      })
      this.loading = false
    })
    if(this.$route.query.listQuery) {
      this.listQuery = Object.assign({}, JSON.parse(this.$route.query.listQuery))
      this.loading = true
      this.$get('teacher/learningState/courseListByGrade', {grade: this.listQuery.grade}).then(res => {
        if(res.status===1) {
          this.listQuery.courseId = ''
          this.listQuery.classId = ''
          this.curriculumOptions = []
          this.classOptions = []
          this.curriculumOptions = res.data
          this.listQuery = Object.assign({}, JSON.parse(this.$route.query.listQuery))
          this.loading = true
          this.$get('/teacher/learningState/classesListByGradeAndCourseId', {grade: this.listQuery.grade, courseId: this.listQuery.courseId}).then(res => {
            if(res.status===1) {
              this.listQuery.classId = ''
              this.classOptions = []
              this.classOptions = res.data
              this.listQuery = Object.assign({}, JSON.parse(this.$route.query.listQuery))
              this.getList()
            } else {
              this.$notify.error({
                title: '提示',
                message: '获取带课班级失败！请刷新重试',
                duration: 2000
              })
            }
            this.loading = false
          }).catch(err => {
            this.$notify.error({
              title: '提示',
              message: '获取带课班级失败！请刷新重试',
              duration: 2000
            })
            this.loading = false
          })
        } else {
          this.$notify.error({
            title: '提示',
            message: '获取课程名称失败！请刷新重试',
            duration: 2000
          })
        }
        this.loading = false
      }).catch(err => {
        this.$notify.error({
          title: '提示',
          message: '获取课程名称失败！请刷新重试',
          duration: 2000
        })
        this.loading = false
      })
    }
  },
  methods: {
    getList() {
      if(!this.listQuery.grade) {
        this.$notify.warning({
          title: '提示',
          message: '请选择所属学年!',
          duration: 2000
        })
        return
      }
      if(!this.listQuery.courseId) {
        this.$notify.warning({
          title: '提示',
          message: '请选择课程名称!',
          duration: 2000
        })
        return
      }
      this.flag = false
      this.loading = true
      this.$post('/teacher/learningState/learningStateQuery', this.listQuery).then(res => {
        if(res.status===1) {
          // this.$notify.success({
          //   title: '提示',
          //   message: '获取列表成功!',
          //   duration: 2000
          // })
          this.total = res.data.myPage.rowCount
          let title = []
          res.data.title.forEach(item => {
            title.push({
              grade: item
            })
          })
          this.tableInfo = title
          this.tableData = res.data.myPage.studentList
        } else {
          this.$notify.error({
            title: '提示',
            message: '获取列表失败,请刷新重试!',
            duration: 2000
          })
        }
        this.loading = false
      }).catch(err => {
        this.$notify.error({
          title: '提示',
          message: '获取列表失败,请刷新重试!',
          duration: 2000
        })
        this.loading = false
      })
    },
    gradeChange() {
      this.loading = true
      this.$get('teacher/learningState/courseListByGrade', {grade: this.listQuery.grade}).then(res => {
        if(res.status===1) {
          this.listQuery.courseId = ''
          this.listQuery.classId = ''
          this.curriculumOptions = []
          this.classOptions = []
          this.curriculumOptions = res.data
        } else {
          this.$notify.error({
            title: '提示',
            message: '获取课程名称失败！请刷新重试',
            duration: 2000
          })
        }
        this.loading = false
      }).catch(err => {
        this.$notify.error({
          title: '提示',
          message: '获取课程名称失败！请刷新重试',
          duration: 2000
        })
        this.loading = false
      })
    },
    courseChange() {
      this.loading = true
      this.$get('/teacher/learningState/classesListByGradeAndCourseId', {grade: this.listQuery.grade, courseId: this.listQuery.courseId}).then(res => {
        if(res.status===1) {
          this.listQuery.classId = ''
          this.classOptions = []
          this.classOptions = res.data
        } else {
          this.$notify.error({
            title: '提示',
            message: '获取带课班级失败！请刷新重试',
            duration: 2000
          })
        }
        this.loading = false
      }).catch(err => {
        this.$notify.error({
          title: '提示',
          message: '获取带课班级失败！请刷新重试',
          duration: 2000
        })
        this.loading = false
      })
    },
    headInfo(item) {
      this.pageTitle = item[1]
    },
    search() {
      this.getList()
    },
    handleSizeChange(val) {
      this.listQuery.pageSize = val
      this.getList()
    },
    handleCurrentChange(val) {
      this.listQuery.pageNum = val
      this.getList()
    },  
    handleSelectionChange(val) {  
      const arr = []
      val.forEach((item, i) => {
        arr.push(item.id)
      })
      this.ids = arr.join(',')
    },
    viewDetail(row) {
      const course = this.curriculumOptions.find((item)=>{
            if (item.id === this.listQuery.courseId) return item
          })
      this.$router.push({
        name: 'statusInfo',
        query: {
          info: JSON.stringify(row),
          course: JSON.stringify(course),
          tableInfo: JSON.stringify(this.tableInfo),
          listQuery: JSON.stringify(this.listQuery)
        }
      })

    },
    sign(row) {
      this.signDetail = row
      this.signDialogVisible = true
      this.studentId = row.id
      this.content = ''
    },
    cancelSign(row) {
      this.$confirm('是否取消重点标记?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning',
        closeOnClickModal: false
      }).then(() => {
        this.$get('/teacher/learningState/deleteKeyMark', {markId: row.markId}).then(res => {
          if(res.status===1) {
            this.$notify.success({
              title: '提示',
              message: '已取消重点标记!',
              duration: 2000
            })
            this.getList()
          } else {
            this.$notify.error({
              title: '提示',
              message: '取消重点标记失败！请稍后重试',
              duration: 2000
            })
          }
        }).catch(err => {
          this.$notify.error({
            title: '提示',
            message: '取消重点标记失败！请稍后重试',
            duration: 2000
          })
        })
      }).catch(() => {
        
      })
    },
    handleSubmuit() {
      if(this.content.trim() === '') {
        this.$notify.warning({
          title: '提示',
          message: '请添加备注!',
          duration: 2000
        })
      } else {
        this.loading = true
        this.$post('/teacher/learningState/keyMark', {studentId: this.signDetail.id, courseId: this.listQuery.courseId, comment: this.content}).then(res => {
          if(res.status===1) {
            this.$notify.success({
              title: '提示',
              message: '重点标记成功!',
              duration: 2000
            })
            this.signDialogVisible = false
            this.getList()
          } else {
            this.$notify.error({
              title: '提示',
              message: '重点标记失败！请重试',
              duration: 2000
            })
          }
          this.loading = false
        }).catch(err => {
          this.$notify.error({
            title: '提示',
            message: '重点标记失败！请重试',
            duration: 2000
          })
          this.loading = false
        })
      }
    },
    exportList() {
      if(!this.listQuery.grade) {
        this.$notify.warning({
          title: '提示',
          message: '请选择所属学年!',
          duration: 2000
        })
        return
      }
      if(!this.listQuery.courseId) {
        this.$notify.warning({
          title: '提示',
          message: '请选择课程名称!',
          duration: 2000
        })
        return
      }
      let tmpObj = JSON.parse(JSON.stringify(this.listQuery))
      delete tmpObj.pageNum
      delete tmpObj.pageSize
      let tmp = '?'
      for(let key in tmpObj) {
        tmp += key + '=' + tmpObj[key] + '&'
      }
      tmp = tmp.slice(0, -1)
      window.location.href = this.API_URL + '/teacher/learningState/export' + tmp
    }
  }
};
</script>

<style lang='scss'>
@import "../../styles/center.scss";
.studentLearningStatus {
  margin-bottom: 100px;
  .center_content {
    .center_content_title {
      position: relative;
      .derive-btn {
        position: absolute;
        right: 0;
        top: 10px;
        .el-button {
          // padding: 7px 23px 3px 23px;
          float: left;
          i,
          span {
            font-size: 12px;
            color: #fff;
          }
          i {
            margin-top: 0;
          }
          span {
            font-weight: normal;
            margin-left: 0;
          }
        }
      }
    }
    .center_content_search {
      height: 280px;
      padding: 30px 60px;
      box-sizing: border-box;
      background-image: url('../../assets/img/bg-chaxun.png') !important;
      background-size: 100% 100%;
      .el-input__inner {
        background-color: #16be95;
        color: #fff
      }
      input::placeholder{
        color:#fff;
      }
      .el-select .el-input.is-focus .el-input__inner,
      .el-input__inner:focus {
        border-color: #fff;
      }
      .el-form-item__label {
        color: #fff;
        font-size:16px;
        font-weight: bold;
      }
    }
    .el-row {
      .el-select {
        width: 90%;
      }
    }
  }
}
</style>



